import React, { useEffect, useState } from "react";

//import { PageTitle } from '../../layout-components';
import { IconButton, Box, Card, CardContent } from "@material-ui/core";
import { InputGroup, FormControl, Button, DropdownButton, Dropdown } from "react-bootstrap";
import axios from "axios";
import { Row, Col, Tabs, Tab } from "react-bootstrap";
import BootstrapTable from "react-bootstrap-table-next";
import { CSVLink } from "react-csv";
import paginationFactory from "react-bootstrap-table2-paginator";
import filterFactory, { textFilter } from 'react-bootstrap-table2-filter';
import TemplatePrintAdmin from '../../Template/TemplatePrintAdmin'
import QRcode from '../../QRcode/QRcode'
import * as FileSaver from 'file-saver';
import * as XLSX from 'xlsx';
import Aux from "../../../hoc/_Aux";
import MessageModal from "./../../../Components/Alert/Alert";
import ModalModifierColis from '../../ModificationColis/Modal.modification.colis'
import moment from "moment"
import DispatchRetour from './Dispatch.Retour.Delivery/Dispatch.Delivery.Retour'
var urlbackend = require("../../../env/env")();
const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
const fileExtension = '.xlsx';
function ListPickupAdmin() {


    const [pendingPackages, setpendingPackages] = useState([])
    const [showMessageParams, setShowMessageParams] = useState({
        display: false,
        type: "success",
        message: null
    })
    const [drivers, setdrivers] = useState([])
    //  const [driver, setdriver] = useState([])
    //     };
    // }
    function priceFormatter(column, colIndex, { sortElement, filterElement }) {
        return (
            <div style={{ display: 'flex', flexDirection: 'column' }}>
                {filterElement}
                {column.text}
                {sortElement}<span className="fa fa-filter"></span>
            </div>
        );
    }
 
    const Impression = (cell, row, rowIndex, formatExtraData) => {
        return (
            <div>

                <TemplatePrintAdmin Template={row} providerindex={row.colis}  > </TemplatePrintAdmin>


            </div>
        );
    };
    const getdata = () => {


        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        /*    axios.get(
              urlbackend + "/package/pending?current_page=1&per_page=500",
              config
          ).then((response)=>{
              this.setState({ pendingPackages: response.data.data.data });
              console.log(response.data)
          })
          axios.get(urlbackend + "/driver", config).then((response)=>{
              this.setState({ drivers: response.data.data });

          }) */
        var getpendingPackages = axios.get(
            urlbackend + "/delivery/retour/list?current_page=1&per_page=500",
            config
        )


        var getdrivers = axios.get(urlbackend + "/driver", config);
        // setpendingPackages([]);
        // setongoingPackages([]);
        // setenlevePackages([]);
        axios
            .all([getpendingPackages, getdrivers])
            .then(
                axios.spread((...response) => {

                    setpendingPackages(response[0].data.data.data);

                    setdrivers(response[1].data.data);

                    /*     this.setState({ pendingPackages: response[0].data.data.data });
                        this.setState({ ongoingPackages: response[1].data.content });
                        this.setState({ enlevePackages: response[2].data.content });

                        this.setState({ drivers: response[3].data.content }); */
                })
            )
            .catch((errors) => {
                // react on errors.
                console.error(errors);
            });
    }
    useEffect(() => {
        getdata()
    }, [])
    const nextstep = (id) => {
        setShowMessageParams({
            display: false,
            type: "success",
            message: null
        });
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.put(urlbackend + "/forcing/pickup/to_done", { "colis": id }, config).then((response) => {
            setShowMessageParams({
                display: true,
                type: "success",
                message: "Forçage effectué avec succès !"
            });
            getdata()


        }).catch(() => {
            setShowMessageParams({
                display: true,
                type: "danger",
                message: "Une erreur s'est produite lors du forçage !"
            });
            console.log("no")
        })

    }
    const nextstepongoing = (id) => {
        setShowMessageParams({
            display: false,
            type: "success",
            message: null
        });
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.put(urlbackend + "/forcing/pickup/to_ongoing", { "colis": id }, config).then((response) => {
            setShowMessageParams({
                display: true,
                type: "success",
                message: "Forçage effectué avec succès !"
            });
            getdata()


        }).catch(() => {
            setShowMessageParams({
                display: true,
                type: "danger",
                message: "Une erreur s'est produite lors du forçage !"
            });
            console.log("no")
        })

    }
    const forcageongoing = (cell, row, rowIndex, formatExtraData) => {


        return (
            <Button
                variant="warning"
                onClick={() => nextstepongoing(row.colis.id)}
            >
                {" "}
                <span className="fa fa-forward"></span>
            </Button>
        );
    }
    const forcagedone = (cell, row, rowIndex, formatExtraData) => {


        return (
            <Button
                variant="warning"
                onClick={() => nextstep(row.colis.id)}
            >
                {" "}
                <span className="fa fa-forward"></span>
            </Button>
        );
    }




    const flowlink = (cell, row, rowIndex, formatExtraData) => {
        //var k = drivers
        return (
            <InputGroup >
                <FormControl
                    as="select"
                    id="{driver}"
                    onChange={(value) =>
                        savedriver(
                            value.target.value,
                            row.colis.id
                        )
                    }
                    custom
                >
                    <option>Sélectionner un chauffeur</option>

                    {drivers.map((driver, index) => (
                        <option
                            className=" badge badge-info"
                            key={driver.id}
                            value={driver.id}
                        >
                            {driver.username}
                        </option>
                    ))}
                </FormControl>
            </InputGroup>
        );
    }
    const flowlinkdipsatch = (cell, row, rowIndex, formatExtraData) => {
        //var k = drivers
        return (
            <InputGroup >
                <FormControl
                    as="select"
                    id="{driver}"
                    onChange={(value) =>
                        savedrivertocolis(
                            value.target.value,
                            row.colis.id
                        )
                    }
                    custom
                >
                    <option>Sélectionner un chauffeur</option>

                    {drivers.map((driver, index) => (
                        <option
                            className=" badge badge-info"
                            key={driver.id}
                            value={driver.id}
                        >
                            {driver.username}
                        </option>
                    ))}
                </FormControl>
            </InputGroup>
        );
    }

    const columns = [
        {
            dataField: "colis.index_colis",
            text: "Numéro colis",
            headerFormatter: priceFormatter,
            filter: textFilter(),
            sort: true

        },

        {
            dataField: "colis.provider.username",
            text: "Expiditeur",
            formatter: (cell, row) => {
                return (
                    <div className="d-flex align-items-center">
                        <a
                            href="#/"
                            onClick={(e) => e.preventDefault()}
                            className="font-weight-bold text-black"
                            title="..."
                        >


                            {row.colis.provider.username} :{" "}
                            {row.colis.provider.phone_number}
                        </a>

                    </div>);
            },
            headerFormatter: priceFormatter,

            filter: textFilter(),



        },
        {
            dataField: "colis.name_complete",
            text: "Client",
            headerFormatter: priceFormatter,
            filter: textFilter(),


        },
        {
            dataField: "colis.address",
            text: "addresse de client",
            headerFormatter: priceFormatter,
            filter: textFilter(),

            style: { textAlign: "center" },


            formatter: (cell, row) => {
                return (<div>{`${row.colis.gouvernorat} `} <br></br> {`${row.colis.address}`}</div>);
            }
            // formatter: this.linkFollow,

        },
        {
            dataField: "colis.phone_number",
            text: "N. de téléphone",
            headerFormatter: priceFormatter,
            filter: textFilter(),

        },




        {
            dataField: "colis.phone_number",

            text: "action",
            style: { textAlign: "center" },
            formatter: flowlink,
            attrs: function callback(cell, row, rowIndex, colIndex) {
                console.log("hello")
            }
        },

        {
            dataField: "colis.phone_number",

            text: "Dipstch colis livraison",
            style: { textAlign: "center" },
            formatter: flowlinkdipsatch,
            attrs: function callback(cell, row, rowIndex, colIndex) {
                console.log("hello")
            }
        },
       

        

        {
            dataField: "colis.name_complete",
            text: "qrcode",
            formatter: (cell, row) => {
                return (<QRcode url={row.colis.id} />);
            }


        },
    ];

const savedrivertocolis = (iddriver, idpacket) => {
    const config = {
        headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Headers': 'Authorization',
            "x-access-token": localStorage.getItem('token'),
            'Authorization': 'Bearer ' + localStorage.getItem('token'),
        },
    };
    axios.put(
        urlbackend + "/colis/from_retour_final/to_retour_provisoire/"+idpacket,
        { driver: iddriver,  },
        config
    )
        .then((response) => {
            alert("add Succefull");
            console.log("response.data", response.data);
            getdata()
        })
        .catch((e) => {
            console.log(e);
        });
}

    const savedriver = (iddriver, idpacket) => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.put(
            urlbackend + "/colis/to_retour",
            { driver: iddriver, colis: [idpacket] },
            config
        )
            .then((response) => {
                alert("add Succefull");
                console.log("response.data", response.data);
                getdata()
            })
            .catch((e) => {
                console.log(e);
            });
    }
    useEffect(() => {
        // console.log("show message value:",showMessage);
    }, [showMessageParams]);
    const downloadfile = (type, list) => {
        if (type === "csv") {
            var listdownload = list.map((e) => ({
                "Numéro colis": e.colis.index_colis, "Expéditeur": e.colis.provider.username, "N. de téléphone Expéditeur": e.colis.provider.phone_number,

                "client ": e.colis.name_complete, "N. de téléphone": e.colis.phone_number, "gouvernorat": e.colis.gouvernorat, "addresse": e.colis.city + ":" + e.colis.address, "prix": e.colis.price, "type paiment": e.colis.payment_mode, "type d'envoi": e.colis.type_envoi,
                "date created": moment(e.colis.created_at).format("DD/MM/YYYY, HH:mm")

            }))
            const ws = XLSX.utils.json_to_sheet(listdownload);
            const wb = { Sheets: { 'data': ws }, SheetNames: ['data'] };
            const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
            const data = new Blob([excelBuffer], { type: fileType });
            FileSaver.saveAs(data, "fileName" + fileExtension);
        } else {

            alert("save pdf")
        }
    }
    // render() {
    return (
        <Aux>

            <Row>
                <Col>
                    <button className="btn btn-info" onClick={() => getdata()} > <span className="fa fa-refresh"></span>  Actualiser</button>


                    <hr />
                    <Tabs defaultActiveKey="home">
                        <Tab eventKey="home" title="En attente">
                            {/* <div>value:{showMessage.toString()}</div> */}
                            {showMessageParams.display && <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />}
                            <Card className="card-box mb-4">
                                <div className="card-header">
                                    {/*   <Button
                                        variant="success"
                                        onClick={() => nextstep("ee")}
                                    >
                                        {" "}
                                        <span className="fa fa-download"></span>
                                    </Button> */}
                                    <DropdownButton
                                        title={<span className="fa fa-download"></span>}
                                        variant="success"
                                        id={`dropdown-variants-success`}
                                        key="success"
                                        onSelect={(e) => downloadfile(e, pendingPackages)}
                                    >
                                        <Dropdown.Item eventKey="pdf">pdf</Dropdown.Item>
                                        <Dropdown.Item eventKey="csv">excel </Dropdown.Item>

                                    </DropdownButton>
                                    <div className="card-header--title">
                                        {/* <b>This table card has custom content</b> */}
                                    </div>
                                    <Box className="card-header--actions">
                                        <IconButton
                                            size="small"
                                            color="primary"
                                            className="text-primary"
                                            title="View details"
                                        ></IconButton>
                                    </Box>
                                </div>
                                <CardContent className="p-0">
                                    <div className="table-responsive">
                                        <div className="table-responsive" style={{ height: "2000px" }}>
                                            <div className="app-row">
                                                <div className="app-col-xs-12 app-col-md-12">
                                                    <BootstrapTable
                                                        striped
                                                        bordered
                                                        hover
                                                        variant="dark"
                                                        //   expandRow={expandRow}
                                                        bootstrap4={true}
                                                        keyField="id"
                                                        data={pendingPackages}
                                                        columns={columns}
                                                        pagination={paginationFactory({
                                                            sizePerPage: 15,
                                                            sizePerPageList: [5, 10, 15, 20, 25],
                                                        })}
                                                        filter={filterFactory()}
                                                        headerClasses="table thead-light mb-0"
                                                    ></BootstrapTable>




                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </CardContent>
                            </Card>
                        </Tab>

                    </Tabs>
                </Col>
            </Row>
        </Aux>
    );

}

export default ListPickupAdmin;

